@import "../../Variables.scss";

.hero {
    min-height: 700px;
    height: 100%;
    padding-top: 200px;
    position: relative;
    overflow: hidden;

    // &::before,
    // &::after {
    //     content: '';
    //     position: absolute;
    //     bottom: -102px;
    //     width: 100%;
    //     height: 1000px;
    //     @include transition(1s ease-in-out);
    // }

    // &::before {
    //     background-color: $primary;
    //     // clip-path: polygon(0 78%, 100% 87%, 100% 100%, 0% 100%);
    //     clip-path: ellipse(50% 27% at 5% 104%);
    // }

    // &::after {
    //     background-color: $secondary-dark;
    //     // clip-path: polygon(0 85%, 100% 75%, 100% 100%, 0% 100%);
    //     clip-path: ellipse(79% 25% at 62% 104%);
    // }
    .egg {
        position: absolute;
        bottom: -102px;
        width: 100%;
        height: 1000px;
        pointer-events: none;
        @include transition(0.5s ease-in-out);
        &.egg-yellow {
            background-color: $primary;
            clip-path: ellipse(50% 27% at 5% 104%);
        }
        &.egg-purple {
            background-color: $secondary-dark;
            clip-path: ellipse(82% 25% at 67% 104%);
        }
    }
    &.inactive {
        .egg.egg-yellow {
            clip-path: ellipse(80% 117% at 5% 104%);
        }
        .egg.egg-purple {
            clip-path: ellipse(142% 105% at 67% 104%);
        }
    }
    
    .logo-container {
        position: relative;
        width: 200px;
        margin: 0 auto;
        margin-bottom: 50px;
        z-index: 0;
        .logo {
            height: 100%;
            width: 100%;
            position: relative;
            z-index: 1;
        }
        &::before,
        &::after {
            content: '';
            z-index: 0;
            position: absolute;
            height: 51px;
            width: 250px;
            border-radius: 50px;
            box-shadow: -3px 3px 3px #0000007a;
        }
        &::before {
            top: 73px;
            left: -10px;
            background-color: $secondary;
            transform: rotate(-30deg) translateY(-50%);
        }
        &::after {
            top: 146px;
            left: -35px;
            background-color: $primary;
            transform: rotate(-30deg) translateY(-50%);
        }
    }
    
    .hero-description {
        text-align: center;
        -webkit-user-select: none; /* Safari */        
        -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* IE10+/Edge */
        user-select: none; /* Standard */
        h1 {
            font-family: 'CarbonBlock', sans-serif;
            font-size: 85px;
            .capitalize {
                font-size: calc(100% + 20px);
            }
            color: $primary;
            line-height: 90px;
        }
        h2 {
            font-size: 27px;
        }
        p {
            width: 300px;
            margin: 0 auto;
            line-height: 2;
        }
    }
}
